import { Component,OnInit, ElementRef, ViewChild, Input, OnChanges, OnDestroy } from '@angular/core';
import * as Plyr from 'plyr';

@Component({
  selector: 'app-plyr-player',
  templateUrl: './plyr-player.component.html',
  styleUrls: ['./plyr-player.component.scss'],
  standalone: true,
})

export class PlyrPlayerComponent implements OnInit, OnChanges, OnDestroy {
  @ViewChild('player', { static: false }) playerRef!: ElementRef;
  @Input() videoSrc!: string;  // URL dinamico del video
  player!: Plyr;

  ngOnInit(): void {
    
  }
  
  ionViewDidEnter() {
    if (this.playerRef?.nativeElement) {
      this.initializePlayer();
    }
  }
  ngOnChanges(): void {
    // Aggiorna il video se `videoSrc` cambia
    if (this.player && this.videoSrc) {
      this.updateVideoSource();
    }
  }

  initializePlayer() {
    this.player = new Plyr(this.playerRef.nativeElement, {
      controls: ['play', 'progress', 'volume', 'fullscreen'],
    });

    if (this.videoSrc) {
      this.updateVideoSource();
    }
  }

  updateVideoSource() {
    this.player.source = {
      type: 'video',
      sources: [
        {
          src: this.videoSrc,
          type: 'video/mp4'
        }
      ]
    };
  }

  ngOnDestroy(): void {
    if (this.player) {
      this.player.destroy();
    }
  }
}